// frontend/src/components/NavBar.tsx
'use client';

import { useState, useEffect, useRef } from "react"; // Adicionado useRef
import Link from "next/link";
import ApplicationLogo from "./ApplicationLogo";
import NavLink from "./NavLink";
import ThemeToggle from "@/components/menu/ThemeToggle"; // Certifique-se que este está adaptado para next-themes
import { usePathname } from "next/navigation";
import { ChevronDown, Menu as MenuIcon, X as XIcon, UserCircle, Settings, LogOut } from 'lucide-react'; // Ícones adicionados

export default function NavbarLayout() {
  const user = { // Mock de usuário
    name: "Fulano de Tal",
    email: "fulano@example.com",
    // Adicione um avatar se tiver
    // avatarUrl: "/path/to/avatar.png",
  };

  const [showingNavigationDropdown, setShowingNavigationDropdown] = useState(false);
  const [showUserDropdown, setShowUserDropdown] = useState(false);
  const pathname = usePathname();
  const userDropdownRef = useRef<HTMLDivElement>(null); // Ref para o dropdown do usuário
  const mobileMenuButtonRef = useRef<HTMLButtonElement>(null); // Ref para o botão do menu mobile

  const navLinks = [
    // { href: "/", label: "Dashboard", icon: LayoutDashboard }, // Exemplo de link para Dashboard
    { href: "/perguntas", label: "Perguntas" },
    { href: "/questionarios", label: "Questionários" },
    // { href: "/queperg", label: "QuePerg" }, // Talvez não seja um link de menu principal
    { href: "/avaliacao", label: "Avaliações" }, // Nome mais amigável
    { href: "/respostas", label: "Respostas" },
    // { href: "/usuAval", label: "Usuário Aval." }, // Pode estar dentro de Avaliações ou Usuários
    { href: "/usuario", label: "Usuários" },
  ];

  // Efeito para fechar dropdowns ao clicar fora
  useEffect(() => {
    const handleClickOutside = (event: MouseEvent) => {
      if (showUserDropdown && userDropdownRef.current && !userDropdownRef.current.contains(event.target as Node)) {
        setShowUserDropdown(false);
      }
      // Lógica similar para o menu mobile se ele não for full-screen
      if (showingNavigationDropdown && mobileMenuButtonRef.current && !mobileMenuButtonRef.current.contains(event.target as Node) && !(event.target as HTMLElement).closest('#mobile-menu')) {
         // setShowingNavigationDropdown(false); // Descomente se o menu mobile não ocupar a tela toda
      }
    };
    document.addEventListener('mousedown', handleClickOutside);
    return () => {
      document.removeEventListener('mousedown', handleClickOutside);
    };
  }, [showUserDropdown, showingNavigationDropdown]);


  return (
    <nav className="fixed top-0 left-0 right-0 z-50 bg-element-bg border-b border-main-border shadow-sm print:hidden">
      <div className="mx-auto max-w-screen-xl px-4 sm:px-6 lg:px-8">
        <div className="flex h-16 items-center justify-between">
          {/* Seção Esquerda: Logo e Links Desktop */}
          <div className="flex items-center">
            <Link href="/" className="flex-shrink-0 flex items-center gap-2">
              <ApplicationLogo className="block h-8 w-auto text-primary" />
              <span className="font-semibold text-lg text-text-base hidden sm:block">Evaluationd</span>
            </Link>
            <div className="hidden md:ml-6 md:flex md:space-x-1 lg:space-x-2"> {/* Reduzido espaço entre links */}
              {navLinks.map((link) => (
                <NavLink 
                  key={link.href} 
                  href={link.href} 
                  active={pathname === link.href || (link.href !== "/" && pathname.startsWith(link.href))}
                  className="px-3 py-2" // Padding consistente
                >
                  {link.label}
                </NavLink>
              ))}
            </div>
          </div>

          {/* Seção Direita: ThemeToggle e User Dropdown */}
          <div className="hidden md:flex md:items-center md:space-x-3"> {/* Reduzido space-x */}
            <ThemeToggle />
            
            <div className="relative" ref={userDropdownRef}>
              <button
                id="user-menu-button" // ID para aria-labelledby
                type="button"
                onClick={() => setShowUserDropdown((prev) => !prev)}
                className="flex items-center rounded-full p-1 text-text-muted hover:text-primary focus:outline-none focus:ring-2 focus:ring-primary focus:ring-offset-2 focus:ring-offset-element-bg"
                aria-expanded={showUserDropdown} // Booleano
                aria-haspopup="true"
              >
                <span className="sr-only">Abrir menu do usuário</span>
                {/* <UserCircle size={24} className="h-7 w-7 rounded-full" /> Avatar placeholder */}
                <span className="text-sm font-medium mr-1 ml-1">{user.name}</span>
                <ChevronDown size={18} aria-hidden="true" />
              </button>

              {showUserDropdown && (
                <div 
                  className="absolute right-0 mt-2 w-56 origin-top-right rounded-md bg-element-bg shadow-lg ring-1 ring-main-border ring-opacity-5 focus:outline-none py-1 z-20" // Adicionado z-20
                  role="menu" 
                  aria-orientation="vertical" 
                  aria-labelledby="user-menu-button"
                >
                  <div className="px-4 py-3">
                    <p className="text-sm text-text-base font-semibold">{user.name}</p>
                    <p className="text-xs text-text-muted truncate">{user.email}</p>
                  </div>
                  <div className="border-t border-main-border my-1"></div>
                  <Link href="/profile" className="flex items-center w-full text-left px-4 py-2 text-sm text-text-base hover:bg-page-bg hover:text-primary" role="menuitem" onClick={() => setShowUserDropdown(false)}>
                    <UserCircle size={16} className="mr-2" /> Meu Perfil
                  </Link>
                  <Link href="/config" className="flex items-center w-full text-left px-4 py-2 text-sm text-text-base hover:bg-page-bg hover:text-primary" role="menuitem" onClick={() => setShowUserDropdown(false)}>
                    <Settings size={16} className="mr-2" /> Configurações
                  </Link>
                  <div className="border-t border-main-border my-1"></div>
                  <button
                    type="button"
                    onClick={() => { alert("Fazer logout"); setShowUserDropdown(false); }} 
                    className="flex items-center w-full text-left px-4 py-2 text-sm text-red-600 dark:text-red-400 hover:bg-red-500/10"
                    role="menuitem"
                  >
                    <LogOut size={16} className="mr-2" /> Sair
                  </button>
                </div>
              )}
            </div>
          </div>

          {/* Botão Mobile para abrir menu */}
          <div className="-mr-2 flex items-center md:hidden">
            <ThemeToggle />
            <button
              ref={mobileMenuButtonRef}
              type="button"
              onClick={() => setShowingNavigationDropdown(!showingNavigationDropdown)}
              className="ml-2 inline-flex items-center justify-center rounded-md p-2 text-text-muted hover:bg-page-bg hover:text-primary focus:outline-none focus:ring-2 focus:ring-inset focus:ring-primary"
              aria-controls="mobile-menu"
              aria-expanded={showingNavigationDropdown} // Booleano
            >
              <span className="sr-only">Abrir menu principal</span>
              {showingNavigationDropdown ? (
                <XIcon className="block h-6 w-6" aria-hidden="true" />
              ) : (
                <MenuIcon className="block h-6 w-6" aria-hidden="true" />
              )}
            </button>
          </div>
        </div>
      </div>

      {/* Menu Dropdown Mobile */}
      {showingNavigationDropdown && (
        <div className="md:hidden border-t border-main-border bg-element-bg shadow-lg" id="mobile-menu">
          <div className="space-y-1 px-2 pb-3 pt-2 sm:px-3">
            {navLinks.map((link) => (
              <NavLink
                key={link.href}
                href={link.href}
                active={pathname === link.href || (link.href !== "/" && pathname.startsWith(link.href))}
                className="block rounded-md px-3 py-2 text-base font-medium"
                onClick={() => setShowingNavigationDropdown(false)} // Fecha o menu ao clicar
              >
                {link.label}
              </NavLink>
            ))}
          </div>
          <div className="border-t border-main-border pb-3 pt-4">
            <div className="flex items-center px-5"> {/* Aumentado padding */}
              {/* <UserCircle size={32} className="h-10 w-10 rounded-full text-text-muted" /> Avatar placeholder */}
              <div className="ml-3">
                <div className="text-base font-medium text-text-base">{user.name}</div>
                <div className="text-sm font-medium text-text-muted">{user.email}</div>
              </div>
            </div>
            <div className="mt-3 space-y-1 px-2">
              <NavLink href="/profile" className="block rounded-md px-3 py-2 text-base font-medium hover:bg-page-bg hover:text-primary" onClick={() => setShowingNavigationDropdown(false)}>
                Meu Perfil
              </NavLink>
              <NavLink href="/config" className="block rounded-md px-3 py-2 text-base font-medium hover:bg-page-bg hover:text-primary" onClick={() => setShowingNavigationDropdown(false)}>
                Configurações
              </NavLink>
              <button
                type="button"
                onClick={() => { alert("Fazer logout"); setShowingNavigationDropdown(false); }}
                className="block w-full text-left rounded-md px-3 py-2 text-base font-medium text-text-base hover:bg-page-bg hover:text-primary"
              >
                Sair
              </button>
            </div>
          </div>
        </div>
      )}
    </nav>
  );
}